import http from '../http'
import url from '../url'
import utils from '../../utils/utils'

// 打开邀请链接
const openShareLink = (params) => {
  return http.request({
    url: url.openShareLink,
    method: 'get',
    params,
  })
}

// 确认邀请链接
const acceptInvite = (data) => {
  return http.request({
    url: url.acceptInvite,
    method: 'post',
    data,
  })
}

// 打开添加链接
const openInvitation = (data) => {
  return http.request({
    url: `${url.openInvitation}${utils.parseParamsToQuery(data)}`,
    method: 'post',
    data,
  })
}

// 接受添加链接
const acceptInvitationAdd = (data) => {
  return http.request({
    url: `${url.acceptInvitationAdd}${utils.parseParamsToQuery(data)}`,
    method: 'post',
    data,
  })
}

export default {
  openShareLink,
  acceptInvite,
  openInvitation,
  acceptInvitationAdd,
}
