<template>
  <div class="content">
    <van-nav-bar title="邀请成为合伙人" @click-left="onClickLeft">
      <template #left>
        <i class="icon-back" />
      </template>
    </van-nav-bar>
    <div class="box" v-if="show">
      <div class="top">{{ storeName }}</div>
      <div class="title">邀请您成为门店合伙人，一起开启海尔新零售！</div>
      <div class="write">
        <div style="padding: 0.2rem 0.32rem 0.2rem 0; background: #fff; position: relative; height: 0.68rem">
          <input type="tel" placeholder="请输入手机号" class="info-mm" v-model="mobileAccount" @blur="verificationTest" maxlength="13" required @keyup="keyup" />
          <img class="editable-clear-x" @click="clear" :src="`${$cdnImageUrl}/login/clear.png`" />
          <div class="line"></div>
        </div>
        <div style="padding: 0.2rem 0.32rem 0.2rem 0; background: #fff; position: relative; height: 0.68rem" v-show="captchaShow">
          <input type="text" v-model="captcha" placeholder="请输入图形验证码" class="info-mm" maxlength="6" @blur="captchaStore" />
          <img class="verify-two" :src="src" alt="" @click="getPhoto" />
          <div class="line"></div>
        </div>
        <div style="padding: 0.2rem 0.32rem 0.2rem 0; background: #fff; position: relative; height: 0.68rem">
          <input type="tel" v-model="messageMM" placeholder="请输入验证码" class="info-mm" maxlength="6" @blur="messageStore" />
          <div class="verify" v-show="!messageMMType" :style="{ color: '#ccc' }">{{ title }}</div>
          <div class="verify" v-show="messageMMType" @click="getMessage" :style="{ color: allow ? '#2979FF' : '#333' }">{{ title }}</div>
          <div class="line"></div>
        </div>
      </div>
      <!-- 同意条款 -->
      <div class="agree">
        <van-checkbox v-model="checked" icon-size="0.3rem" @change="selectChange"></van-checkbox>
        <div class="right">
          <span>若为新用户提交即代表同意</span>
          <a href="http://m.ehaier.com/v3/mstore/sg/helpDetail.html?id=1002" target="blank">《智家掌柜用户服务协议》、</a>
          <a href="http://m.ehaier.com/v3/mstore/sg/helpDetail.html?id=1206" target="blank">《智家掌柜隐私权政策》、</a>
          <a href="http://account.haier.com/html/privacypolicy.html" target="blank">《海尔家电隐私权政策》</a>
        </div>
      </div>
      <button class="btn" @click="submit" :disabled="disabled" :style="{ background: disabled ? '#ccc' : '#2979FF' }">提交</button>
    </div>
    <success v-else :storeName="storeName" :storeId="storeId"></success>
  </div>
</template>
<script>
import personalApi from '@/http/service/login.js'
import Api from '@/http/service/invite.js'
import success from './success'
export default {
  data() {
    return {
      storeName: '店铺名称',
      title: '获取验证码',
      checked: false,
      mobileAccount: '',
      messageMM: '', // 短信验证码
      allow: true,
      phoneReg: /^1[3456789]\d{9}$/,
      captchaShow: false, // 图形验证码控制
      captcha: '', // 图片验证码
      src: '',
      messageMMType: false, // 控制短信验证码显隐
      disabled: true, // 提交按钮验证
      timer: null, // 计时器
      shareId: null, // 邀请人id
      storeId: null, // 店铺id
      show: true,
      getClick: false, // 判断是否点击获取验证码
    }
  },
  components: {
    success,
  },
  created() {
    this.$util.smMsgSdk()
    this.location()
    this.initialize()
    this.androidBack()
  },
  watch: {
    messageMM(val, old) {
      if (val.length !== 6) {
        this.disabled = true
      } else {
        this.disabled = false
      }
    },
  },
  methods: {
    /**
     * @description  android物理返回刷新处理
     */
    androidBack() {
      if (sessionStorage.getItem('invitePhone')) {
        this.mobileAccount = sessionStorage.getItem('invitePhone')
      }
      if (this.mobileAccount.length === 13) {
        this.messageMMType = true
      } else {
        this.messageMMType = false
      }
      if (sessionStorage.getItem('inviteCaptcha')) {
        this.captcha = sessionStorage.getItem('inviteCaptcha')
      }
      if (sessionStorage.getItem('inviteMessage')) {
        this.messageMM = sessionStorage.getItem('inviteMessage')
      }
      if (sessionStorage.getItem('captchaShow') === 'true') {
        this.captchaShow = true
      }
      if (sessionStorage.getItem('checked')) {
        this.checked = sessionStorage.getItem('checked')
      }
      if (sessionStorage.getItem('getClick') === 'true') {
        this.getClick = true
      }
      if (sessionStorage.getItem('inviteAllow') === 'false') {
        this.allow = false
      }
      this.getPhoto()
    },
    /**
     * @description  华为输入法光标错位替代
     */
    keyup() {
      let phone = this.mobileAccount
      phone = phone.replace(/[^0-9]/g, '')
      // 分组成344，并且加上' '
      var temp = []
      temp[0] = phone.slice(0, 3)
      temp[1] = phone.slice(3, 7)
      temp[2] = phone.slice(7)
      if (temp[2].length > 0) {
        temp[1] += ' '
        temp[0] += ' '
      } else if (temp[1].length > 0) {
        temp[0] += ' '
      }
      if (this.mobileAccount.length === 13) {
        this.messageMMType = true
        this.verification()
      } else {
        this.messageMMType = false
      }
      this.mobileAccount = temp.join('')
    },
    /**
     * @description  图片验证
     */
    verification() {
      const dealZh = this.mobileAccount.replace(/\s/g, '')
      if (!this.phoneReg.test(dealZh)) {
        this.$toast('手机号格式不正确，请重新输入')
        return
      }
      let data = {
        mobile: dealZh,
      }
      personalApi.needImg(data).then((res) => {
        if (res.data.data) {
          this.captchaShow = true
          this.getPhoto()
        } else {
          this.captchaShow = false
        }
        sessionStorage.setItem('captchaShow', this.captchaShow)
      })
    },
    /**
     * @description  手机号验证
     */
    verificationTest() {
      const dealZh = this.mobileAccount.replace(/\s/g, '')
      if (!this.phoneReg.test(dealZh)) {
        this.$toast('手机号格式不正确，请重新输入')
      } else {
        sessionStorage.setItem('invitePhone', this.mobileAccount)
      }
    },
    /**
     * @description  图形验证码存储
     */
    captchaStore() {
      sessionStorage.setItem('inviteCaptcha', this.captcha)
    },
    /**
     * @description  短信验证码存储
     */
    messageStore() {
      sessionStorage.setItem('inviteMessage', this.messageMM)
    },
    /**
     * @description  账号清除
     */
    clear() {
      this.mobileAccount = ''
    },
    /**
     * @description  规则选择
     */
    selectChange(value) {
      console.log(666, value)
      this.checked = value
      sessionStorage.setItem('checked', this.checked)
    },
    /**
     * @description  获取图片验证码
     */
    getPhoto() {
      this.src = this.$HOST + '/v3/platform/web/member/register/getImgCaptcha.html?rnd=' + Math.random() + '&flag=' + localStorage.getItem('sessionValue')
    },
    /**
     * @description  登录验证码获取接口
     */
    getMessage() {
      const dealZh = this.mobileAccount.replace(/\s/g, '')
      this.$util.dealSmDeviceId().then((deviceId) => {
        let data = {
          mobile: dealZh,
        }
        if (this.captchaShow) {
          data.imgCaptcha = this.captcha
        }
        // 图片验证校验
        if (this.captchaShow && this.captcha === '') {
          this.$toast('请输入图片验证码')
          return
        }
        // 手机号验证
        if (!this.phoneReg.test(dealZh)) {
          // that.$toast('手机号格式错误')
          return
        }
        if (this.allow) {
          personalApi.register(data, deviceId).then((res) => {
            if (res.data.success === false) {
              this.$toast(res.data.message)
            } else {
              this.allow = false
              sessionStorage.setItem('inviteAllow', true)
              this.$toast('短信验证码发送成功，请及时验证！')
              // 是否点击获取验证码
              this.getClick = true
              sessionStorage.setItem('getClick', true)
              // 倒计时
              let time = 60
              this.timer = setInterval(() => {
                if (time === 0) {
                  this.allow = true
                  clearInterval(this.timer)
                  this.title = '重新获取验证码'
                } else {
                  this.title = time + 's后可重发'
                  this.allow = false
                  time--
                }
              }, 1000)
            }
          })
        }
      })
    },
    /**
     * @description  确认邀请
     */
    submit() {
      const address = this.$store.state.locationInfo
      console.log('------------定位信息', address)
      if (!this.checked) {
        this.$toast('请您认真阅读平台相关协议，然后签收。')
        return
      }
      const dealZh = this.mobileAccount.replace(/\s/g, '')
      if (!this.phoneReg.test(dealZh)) {
        this.$toast('手机号格式不正确，请重新输入')
        return
      }
      if (this.messageMM === '') {
        this.$toast('请输入短信验证码')
        return
      }
      if (!this.getClick) {
        this.$toast('请先获取验证码')
        return
      }
      const data = {
        captcha: this.messageMM,
        memberNumber: dealZh,
        shareId: Number(this.shareId),
        storeId: Number(this.storeId),
        provinceId: Number(address.provinceId),
        cityId: Number(address.cityId),
        regionId: Number(address.districtId),
        regionName: address.region.replace(/\s/g, '')
      }
      Api.acceptInvite(data).then((res) => {
        if (res.data.success) {
          // 登录信息存储
          let result = res.data.data
          let mid = result.mid
          let accessToken = result.accessToken
          let userInfo = {}
          userInfo.username = result.userName
          userInfo.avatarImage = result.avatarImageFileId
          userInfo.loginName = result.loginName
          userInfo.birthday = result.birthday
          userInfo.phone = result.mobile
          userInfo.email = result.email
          userInfo.cartNumber = result.cartNumber
          userInfo.gender = result.gender
          userInfo.isNewUser = result.isNewUser
          userInfo.isStoreMember = result.isStoreMember
          userInfo.nickName = result.nickName
          userInfo.rankName = result.rankName
          userInfo.promotionCode = result.promotionCode
          userInfo.resultSign = result.resultSign
          this.$store.commit('setLoginStatus', true, { root: true })
          this.$store.commit('setmId', result.mid, { root: true })
          this.$store.commit('setUcId', result.ucId, { root: true })
          this.$store.commit('setAcsToken', result.accessToken, { root: true })
          this.$store.commit('setSessionKey', result.sessionKey, { root: true })
          this.$store.commit('setSessionValue', result.sessionValue, { root: true })
          this.$store.commit('setUserInfo', userInfo, { root: true })
          this.$store.commit('setToken', result.token, { root: true })

          // set locaStorage
          localStorage.setItem('isLogin', true)
          localStorage.setItem('ucId', result.ucId)
          localStorage.setItem('token', result.token)
          // localStorage.setItem('sdToken', sdToken)
          localStorage.setItem('accessToken', result.accessToken)
          localStorage.setItem('mId', result.mid)
          localStorage.setItem('sessionKey', result.sessionKey)
          localStorage.setItem('sessionValue', result.sessionValue)
          localStorage.setItem('userInfo', JSON.stringify(userInfo))
          this.$store.dispatch('account/isHasStore', { mid, accessToken })
          if (!res.data.errorCode) {
            this.show = false
          }
        } else {
          this.$toast(res.data.message)
        }
        if (res.data.errorCode) {
          this.$toast(res.data.message)
        }
      })
    },
    /**
     * @description  打开链接
     */
    open() {
      const data = {
        shareId: this.shareId,
        storeId: this.storeId,
      }
      Api.openShareLink(data).then((res) => {
        if (res.data.success) {
          this.storeName = res.data.data.storeName
          this.storeId = res.data.data.storeId
        } else {
          this.$toast(res.data.message)
        }
      })
    },
    /**
     * @description  初始化
     */
    initialize() {
      if (this.$route.query.store && this.$route.query.share) {
        this.storeId = this.$route.query.store
        this.shareId = this.$route.query.share
        this.open()
      }
    },
    /**
     * @description  定位调用
     */
    location() {
      this.$store.dispatch('doLocation').then((res) => {
        if (res === 400) {
          this.$store.dispatch('doLocationInfo', { latitude: '36.12784', longitude: '120.41803' })
        } else {
          this.$store.dispatch('doLocationInfo', res)
        }
      })
    },
    /**
     * @description  返回首页
     */
    onClickLeft() {
      this.$router.push('/')
    },
  },
}
</script>
